<template>
    <div class="main-view">
        <div class="main-header">
            <div class="icon-default" :class="navIcon()" @click="navBackTab"></div>
            <div class="title ellipsis">{{ navTitle() }}</div>
            <el-dropdown class="more-default" placement="bottom-start">
                <div class="icon-default el-icon-more"></div>
                <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item command="a">分享</el-dropdown-item>
                        <el-dropdown-item command="b">更多</el-dropdown-item>
                    </el-dropdown-menu>
                </template>
            </el-dropdown>
        </div>
        <div class="main-layout">
            <keep-alive v-if="$route.meta && $route.meta.keepAlive">
                <router-view></router-view>
            </keep-alive>
            <router-view v-else></router-view>
        </div>
    </div>
</template>

<script>
import { computed, getCurrentInstance } from "vue";
export default {
    name: "ToolbarPageView",
    props: {
        backUrl: [String, Object],
        title: [String]
    },
    setup: props => {
        const { ctx } = getCurrentInstance();
        let routerContent = ctx.$router.currentRoute.value;
        let meta = routerContent.meta;

        const iconType = computed(() => {
            if (meta && meta.menu) {
                return "menu";
            }
            return "back";
        });
        const navIcon = () => {
            if (meta && meta.menu) {
                return "el-icon-s-unfold";
            } else if (meta && meta.icon) {
                return meta.icon;
            }
            return "el-icon-back";
        };
        const navTitle = () => {
            if (props.title) {
                return props.title;
            }
            if (meta && meta.title) {
                return meta.title;
            }
        };
        const navBackTab = () => {
            if (iconType == "menu") {
                ctx.$router.push("/").catch(err => {
                    console.error(err);
                });
            } else {
                if (!props.backUrl) {
                    ctx.$router.back();
                    console.log("没有传递backUrl,直接返回上一级");
                    return;
                }
                this.$router.push(props.backUrl).catch(err => {
                    console.error(err);
                });
            }
        };
        return {
            navIcon,
            navTitle,
            navBackTab
        };
    }
};
</script>

<style scoped>
.main-view {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
}
.main-header {
    height: 0.7rem;
    width: 100%;
    background-color: white;
    display: flex;
    flex-direction: row;
}

.icon-default {
    font-size: 0.3rem;
    color: #000;
    padding: 0.2rem;
    flex-shrink: 0;
}
.more-default {
    width: 0.7rem;
    height: 0.7rem;
}
.title {
    width: calc(100% - 1.4rem);
    height: 100%;
    line-height: 0.7rem;
    text-align: center;
    padding: 0 0.2rem;
    font-size: 0.22rem;
    color: black;
    font-weight: 600;
}
.main-layout {
    height: calc(100% - 0.7rem);
    width: 100%;
}
</style>
